<template>
	<p class="folder">{{ name }}</p>
	<div class="attachment">
		<template v-for="folder in folders">
			<FolderView
				:name="folder.name"
				:folders="folder.folders"
				:files="folder.files"
			/>
		</template>
	</div>
	<template v-for="file in files">
		<FileView :name="file.name" />
	</template>
</template>

<script>
import FileView from './FileView';
export default {
	name: 'FolderView',
	components: { FileView },
	props: {
		name: String,
		folders: Array,
		files: Array,
	},
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.folder {
	color: orangered;

	background: url(https://cdn4.iconfinder.com/data/icons/sabre/snow_sabre_black/128/folder_black.png)
		center center / contain no-repeat;

	width: 110px;
	height: 110px;
	display: flex;
	align-items: center;
	justify-content: center;
	font-weight: bold;
	cursor: pointer;
	transition: transform 0.5s ease-out;
	&:hover {
		transform: scale(1.2);
	}
	&:active {
		color: white;
	}
}
.attachment {
	margin-left: 200px;
}
@media (max-width: 700px) {
	.attachment {
		margin-left: 100px;
	}
}
@media (max-width: 500px) {
	.attachment {
		margin-left: 30px;
	}
}
</style>
